import React, { Component } from "react"
import { Menu } from "semantic-ui-react"
import { withRouter } from "react-router-dom"

const MENU_ITEMS = ["experience", "projects", "skills", "education"]

class Header extends Component {
	state = {
		activeRoute: ""
	}

	componentDidMount() {
		this.setState({
			activeRoute: this.props.location.pathname.substr(1)
		})
	}

	handleRouteClick = (e, { name }) => {
		this.setState({ activeRoute: name })
		this.props.history.push(`/${name}`)
	}

	renderMenuItems = () => {
		const { activeRoute } = this.state

		return MENU_ITEMS.map(item => {
			return (
				<Menu.Item
					name={item}
					active={activeRoute === item}
					onClick={this.handleRouteClick}
					link
					key={item}
				>
					{item.charAt(0).toUpperCase() + item.slice(1)}
				</Menu.Item>
			)
		})
	}

	render() {
		return (
			<Menu pointing secondary fluid widths={4} size={"large"}>
				{this.renderMenuItems()}
			</Menu>
		)
	}
}

export default withRouter(Header)
