import React, { Component } from "react"

const SKILLS = [
	"JavaScript",
	"React",
	"Redux",
	"React Native",
	"Node.js",
	"MongoDB",
	"ES6",
	"REST APIs",
	"TypeScript",
	"GraphQL",
	"Python",
	"MySQL",
	"TDD",
	"Jest"
]

class Skills extends Component {
	renderSkills = () => {
		return SKILLS.map(skill => (
			<span key={skill} className="ui label">
				{skill}
			</span>
		))
	}

	render() {
		return <div className="ui big teal labels">{this.renderSkills()}</div>
	}
}

export default Skills
